var punctuation = [// https://en.wikipedia.org/wiki/Template:%C2%B7
['', ''], ['', ''], ['dot', ''], ['middot', ''], ['', '  '], //yup, oxford comma template
[',', ','], ['1/2', '12'], ['1/3', '13'], ['2/3', '23'], ['1/4', '14'], ['3/4', '34'], ['', ''], ['ndash', ''], ['en dash', ''], ['spaced ndash', '  '], ['', ''], ['mdash', ''], ['em dash', ''], ['number sign', '#'], ['ibeam', 'I'], ['&', '&'], [';', ';'], ['ampersand', '&'], ['snds', '  '], // these '{{^}}' things are nuts, and used as some ilicit spacing thing.
['^', ' '], ['!', '|'], ['\\', ' /'], ['`', '`'], ['=', '='], ['bracket', '['], ['[', '['], ['*', '*'], ['asterisk', '*'], ['long dash', ''], ['clear', '\n\n'], ['h.', '']];
